*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
    background-color: var(--background-color);
}
:root{
  --backgroun-color:rgb(217, 244, 242);
  --nbg:red;
  --border-radius: 10px;
}
/* use of night mode of js */
.nightode{
  --background-color: rgba(9, 7, 41, 100);
  --nColor : white;
  --borderTop:5px solid pink;
 
}
.ni{
  background-color: rgba(9, 7, 41, 0.97);
}

.manuBody{
  margin-top: 50px;
  height: 100%;
  height: 50px;
  background-color:var(--backgroun-color) ;

  box-shadow: 0px 1px 1px rgb(205, 140, 140);

  
}
.manuSection{

  display: flex;


}
.logo{
  height: 50px;
  width: 50px;
  background-color: antiquewhite;
  margin-left: 10%;
  text-align: center;
  border-radius: 50px;

}
.manubar{
  margin-left: 6rem;
 
  
 
}
.nav .active{
  color: red;
}
.nav{
 position: relative;
 top: 10px;
 font-size: 18px;
margin-left:4rem;
text-decoration: none;
color: var(--nColor);
  

}
.nav:hover{
  color: pink;
}
.sing{
  margin-left:10%;

}
.nav1{
  position:relative;
  top: 8px;
  font-size: 20px;
  margin-left: 4rem;
  text-decoration: none;
  color: var(--nColor);

}
.nav1:hover{
  color: pink;
}
.count{
 
  background-color: rgb(113, 106, 241);
  color:white;
  border-radius: 10px;
  padding:0px 5px;
 

}
/* serching */
.search{
  position: fixed;
  top: 0px;
  height: 50px;
  width: 100%;
  background-color: rgb(82, 201, 201);
  text-align:center;
  z-index: 1;
  color: var(--nColor);
}
.search label input{
  width: 50%;
  height: 35px;
  margin-top: 10px;
  border-radius: 10px;
  background-color: #c9e7ef;
  
}
/* use javascript serching NightMode start */
.serchingNightmode{
  background-color:rgb(18, 45, 96);

}
/* use javascript serching NightMode  end*/
/*savascript use  */
.serch-top{
  margin-top: 1px;
}
.search label{
  font-size: 20px;
  font-weight: bold;
}
/* catagory Section  */
.catagory{
  height: 55vh;
  width: 20%;
  background-color: rgb(16, 130, 130);
  color: #0cfcf0;
  margin-top: 50px;
  margin-left: .5%;
  border-radius: 10px;
}
.catagory h2{
  margin: 10px 15px;
}
.catagory h3{
  
  margin: 10px 15px;
  transition: all .3s;
}
.catagory h3:hover{
  
  margin: 10px 15px;
  font-size: 20px;
  color: #eae6f0;
}


/* Home section */

.homediv{
  margin-top: 1px;
  position: relative;
  height: 400px;
  width: 70%;
  left: 25%;
  top: 10px;
  border-radius: 10px;
  background-image:linear-gradient(10deg,hotpink,#35a8f56f,red),url('../public/img/back2.jpg');
}
.homediv{
  display: flex;
  margin-top: 50px;
}
.textItem{
  position: relative;
  top: 100px;
  left: 10%;
}
.imgdiv{
  position: absolute;
  left: 60%;
 top: 15%;
}
.title{
  text-transform: capitalize;
}
.shopeNow{
  position: absolute;
  top: 100px;

}
.shopeNow button{
  height: 30px;
  width: 200px;
  border: 2px solid black;
  background-color: #c9e7ef;
  border-radius: 10px;
}
                        /* Phone section to use home section all css*/
.prolisting{
  position: relative;

  width: 80%;
  left: 13%;
  top: 100px;

  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.prodiv{
  position: relative;
  margin: 10px;
  height: 300px;
  min-width: 250px;
border:2px dotted orchid;
border-bottom: none;
  border-radius: 20px;
  color:rgb(19, 2, 2);
  transition: all 1s;
  
}
.priceText{
 margin-top:20px ;
 color: var(--nColor);
}
.prohoverDiv{
  position: absolute;
  height: 30px;
  width: 300px;
  background-color: #efd2c9;
  z-index: 1;
}
.prodiv:hover{
 background-color: hotpink;
 box-shadow: 2px 2px 10px navy;
 transform: scale(1.3);
 z-index:1;
}
.shopeBtn{
  color: rgb(249, 250, 254);
  padding: 10px;
  background-color: navy;

  border-radius: 10px;
}
/* by now section */
.cartdata{
  position: relative;
  top: 100px;
  left: 30%;
  display: flex;
}
.prodetels{
  height: 300px;
  width: 300px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 3px 5px 10px rgb(219, 212, 197);
  background-color:rgb(212, 209, 209);
}
.prodect-title{
  height: 400px;
  width: 20%;
  margin-left: 5%;
  color: var(--nColor);
}
.size , .propic{
  height: 30px;
  width: 100px;
  border-radius: 5px;
  text-align: center;
}
.proDetelsText{
  font-size: 1rem;
  letter-spacing: 1px;
  line-height: 1.5;
  text-align: justify;
}
.byNowBtn{
  margin-top: 50px;
  width: 200px;
  margin-left:-200px;
}
.byNowBtn button{
  width: 250px;
  height: 30px;
  border-radius: 10px;
  background-color: rgba(41, 174, 211, 0.434);
}







.coumment{
  position: relative;

  top:250px;
  height: 300px;
  width: 60%;
  left: 20%;
  text-align: center;
  border-radius: 10px;
  overflow-y: auto;
  background-color: rgb(82, 201, 201);
}

.cummantMap{
  height: 100px;
  width: 60%;
  margin-left: 15%;
  margin-top: 30px;
  border-radius: 10px;
  background-color:#70e4f9;
  display: flex;
  box-shadow: 5px 5px 10px rgb(116, 163, 239);
}
.commantImg{
  height: 100px;
  width: 20%;

  background-color: #0cfcf0;
  border-radius: 20px;
  margin-left: -20px;
}
.commantImg img{
  
  margin-left: -10%;
}
.commantInputDiv input{
  margin-top: 10px;
  margin-left: -30px;
  border-radius: 10px;
  width: 70%;
  height: 35px;
  background-color: rgb(185, 243, 243);
  text-align: center;
}
.commantInputDiv button{
width: 10%;
height: 35px;
border-radius: 10px;
background-color: aqua;

}
.commanttext{
margin-left:10%;
overflow: hidden;
padding:20px;
width: 70%;

}

/* singSection start  */
.singSection{
  position: relative;
  left: 40%;
  height: 300px;
  width: 50%;
 

}
.browsherDiv{
  position: absolute;
  top:55vh;
  display: flex;

  left: 10%;
  
}
.browsherDiv h3{
  margin-left: 20px;
}
.welcomdiv{
  position: absolute;
  top: 50px;
  left: 16%;
}
.welcomdiv h1{
  font-size: 70px;
  
  
 }
.singdiv{
 position: absolute;
 top: 20vh;
 width: 50%;
}
.singdiv input{
  margin: 20px;
  width:100%;
  height: 35px;
 border-top: none;
 border-left: none;
 border-right: none;
 outline-style: none;
 color: var(--nColor);
background-color: var(--background-color);
 border-bottom:3px solid rgb(243, 48, 191);
 
}
.singdiv input:hover{
  border-bottom: 3px solid rgb(74, 228, 100);

}
.Singbtn{
  position: absolute;
  top: 330px;
  left: 5%;
}
.Singbtn button{
  height: 35px;
  width:300px;
  font-size: 17px;
  font-weight: bold;
  border-radius: 10px;
}
.SingInSection{
  position: absolute;
  left: 80%;
  top: 60px;
}
.SingInBtn{
  
  color:rgb(35, 25, 25);
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 20px;
  text-decoration: none;
  font-weight: bold;

  background-color: rgb(244, 87, 186);

}

/* footerSection start  */
.FooterSection{
  position: relative;
  top: 25vh;
  height: 45vh;
  width: 100%;

 border-radius: 10px;

 background-color: rgba(9, 7, 41, 0.97);
 border-top: var(--borderTop);

 display: flex;

}
.FooterSection div{
  margin: 50px 80px;
}
.FooterSection div p{
  margin-top: 7px;
  font-size: 17px;
  color: #f1aeae;
  transition:all .6s;
}
.FooterSection div p:hover{
  color: #a1c4f8;
  font-size: 20px;
  
}
.FooterSection div h3{
  color: #0cfcf0;
}
.Subscribe{
   position: absolute;
   left: 65%;
}
.Subscribe input{
  height: 35px;
  border-radius: 10px;
  border-color: #70e4f9;
  width: 20rem;
}
.Subscribe button{

  width: 100px;
  height: 35px;
  margin-top: 40px;
  border-radius: 5px;
  border-color: #0cfcf0;
  font-size: 17px;
  box-shadow: 2px 2px 5px rgb(151, 229, 247);

}
.Subscribe button:hover{
  background-color: darkblue;
  color: white;
}
.socialMedia{
  position: absolute;
  top: 200px;
  left: 30%;
}
.socialMedia span{
  font-size: 25px;
  margin-left: 60px;
  transition:all .5s;
}
.socialMedia span:hover{
  font-size: 40px;


}
/* AboutSection start */
.AboutSection{
position: relative;
  width: 50%;
  left: 33%;
  top:50px;

}
.Qustion{
